import Swiper from 'swiper';
import { FreeMode, Pagination } from 'swiper/modules';
import 'swiper/css';
import 'swiper/scss/scrollbar';
import 'swiper/css/free-mode';
import { buildSwiper, removeSwiper } from './buildSwiper';

import 'ScssComponents/executive-team.scss';

export const team = () => {
	const classNames = {
		slider: '.js-executive-team-slider',
		wrapper: '.js-executive-team-wrapper',
	};

	const $sliderWrappers = document.querySelectorAll(classNames.wrapper);

	if (!$sliderWrappers.length) return;

	$sliderWrappers.forEach(($wrapper) => {
		const $slider = $wrapper.querySelector(classNames.slider);
		if (!$slider) return;

		buildSwiper($slider);

		const sliderInstance = new Swiper($slider, {
			modules: [FreeMode],
			observer: true,
			observeParents: true,
			speed: 800,
			slidesPerView: 'auto',
			freeMode: true,
		});
	});
};
